* {
  margin: 0px;
  padding: 0px;
}

ul {
  list-style-type: none;
}

.people-list li:hover {
  background-color: #d3d3d3;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
